export default {
  name: 'first',
  data() {
    return {
      clock: {
        secFrom: '',
        secTo: '',
        minFrom: '',
        minTo: '',
        houForm: '',
        houTo: '',
        scales: []
      }
    }
  },
  created() {
this.clockFun()
  },
  methods: {
    clockFun() {
      //表轴原点坐标
      var cx = 100;
      var cy = 100;

      function shifter(val) {
        return [val, cx, cy].join(' ');
      }
      //时间指针计算公式
      var date = new Date();
      var hoursAngle = 360 * date.getHours() / 12 + date.getMinutes() / 2;
      var minuteAngle = 360 * date.getMinutes() / 60;
      var secAngle = 360 * date.getSeconds() / 60;
      //时间指针位置初始化
      this.clock.secFrom = shifter(secAngle)
      this.clock.secTo = shifter(secAngle + 360)
      this.clock.minFrom = shifter(minuteAngle)
      this.clock.minTo = shifter(minuteAngle + 360)
      this.clock.houForm = shifter(hoursAngle)
      this.clock.houTo = shifter(hoursAngle + 360)

      //表盘刻度创建
      for (var i = 1; i <= 12; i++) {
        let scale = 'rotate(' + (i * 360 / 12) + ' 100 100)'
        this.clock.scales.push(scale)
      }
    }
  }
}



