import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/home'
import first from '@/components/views/my/first'
import second from '@/components/views/my/second'
import lifecycle from '@/components/views/my/lifecycle'
Vue.use(Router)

// console.log(Hello)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: Hello
    },
    {
      path: '/first',
      name: 'clock',
      component: first
    },
    {
      path: '/second',
      name: 'company',
      component: second
    },
    {
      path: '/lifecycle',
      name: 'lifecycle',
      component: lifecycle
    }
  ],
})



