import background from '@/components/common/background'
export default {
  name:'lifecycle',
  components: { background},
data(){
  return {
    a: "Vue生命周期（请打开控制台）",
    b: "Vue.js",
    c: '111'
  }
},
watch:{
  a:{
    handler:function(val,oldVal){
    console.log('new: %s, old: %s', val, oldVal)
  },
  deep:true
},
  b:function(val,oldVal){
    console.log('new: %s, old: %s', val, oldVal)
  }
},
methods:{
  change(){
    this.a = "Vue生命周期,更新数据"
    this.b = "vue.js is great"
  }
},
 beforeCreate: function() {
console.log("创建前")
console.log(this.a)
console.log(this.$el)
},
 created: function() {
console.log("创建之后");
console.log(this.a)
console.log(this.$el)
},
 beforeMount: function() {
console.log("mount之前")
console.log(this.a)
console.log(this.$el)
},
mounted: function() {
console.log("mount之后")
console.log(this.a)
console.log(this.$el)
},
beforeUpdate: function() {
console.log("更新前");
console.log(this.a)
console.log(this.$el)
},
updated: function() {
console.log("更新完成");
console.log(this.a);
console.log(this.$el)
},
beforeDestroy: function() {
console.log("销毁前");
console.log(this.a)
console.log(this.$el)
console.log(this.$el)
},
destroyed: function() {
console.log("已销毁");
console.log(this.a)
console.log(this.$el)
}
}



