export default {
  name: 'music',
  props:{
    url:String,
  },
  data(){
    return {
      isplay: true
    }
  },
  methods:{
        ChangeImg:function(){
          if(this.isplay){
            this.isplay = false;
            this.$refs.music.pause();
          }else{
            this.isplay = true;
            this.$refs.music.play();
            }
        },
  }
}



