export default {
  name: 'navBar',
  props: {

  },
  data() {
    return {
      dis: false,
      disActive: true,
      isActive: false,
      titList: [{
        name: 'Firsh',
        href: '/first'
      }, {
        name: 'Secend',
        href: '/second'
      }, {
        name: 'lifecycle',
        href: '/lifecycle'
      }]
    }
  },
  mounted: function() {
    const that = this;
    WidthWatch();
    window.onresize = WidthWatch;

    function WidthWatch() {
      that.screenWidth = window.innerWidth
      if (that.screenWidth <= 800 && that.dis == false) {
        that.dis = true;
        that.disActive = false;
      } else if (that.screenWidth > 800 && that.dis == true) {
        that.isActive = that.dis = false;
        that.disActive = true;
      }
    }
  },
  methods: {
    distableFun: function() {
      if (!this.isActive) this.isActive = true;
      else this.isActive = false;
    },
    navLi: function() {
      this.isActive = false;
    },
  },
}



